import { GetComponentProps, Anchor, styled } from 'topwrite';
import { ReactComponent as Icon } from '../../images/link.svg';

export default function Link({ props }: GetComponentProps<'leaf-directive'>) {
    const { url, children } = props;

    return <Container href={url}>
        <Icon /><span>{children || url}</span>
    </Container>;
}

export const Container = styled(Anchor)`
    &&& {
        margin: 1em 0;
        box-shadow: rgb(0 0 0 / 12%) 0px 1px 2px;
        padding: 16px;
        border-radius: 4px;
        border: 1px solid var(--ttw-border-color);
        background: var(--ttw-box-background);
        display: flex;
        text-decoration: none;
        align-items: center;
        font-size: 16px;
        font-weight: 500;
        color: var(--ttw-color);

        svg {
            fill: currentColor;
            margin-right: 16px;
            flex-shrink: 0;
        }

        span {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }

        &:hover {
            border-color: var(--ttw-primary-color);
        }
    }
`;
